:root {
	--pattern-border-width: 4px;
	--border-style: solid;
}

.tile-field {
	height: var(--tile-size);
	width: var(--tile-size);
}

.occupied {
	box-shadow: calc(var(--tile-size)/100);
}

.empty {
	background-color: var(--tile-field-background);
}

.pattern-color-0 {
	border: var(--pattern-border-width) var(--border-style) var(--tile-color-0);
}

.pattern-color-1 {
	border: var(--pattern-border-width) var(--border-style) var(--tile-color-1);
}

.pattern-color-2 {
	border: var(--pattern-border-width) var(--border-style) var(--tile-color-2);
}

.pattern-color-3 {
	border: var(--pattern-border-width) var(--border-style) var(--tile-color-3);
}

.pattern-color-4 {
	border: var(--pattern-border-width) var(--border-style) var(--tile-color-4);
}